.registeration,
.login {
  width: 100%;
  height: 700px;

  .formPage {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    .formcontainer {
      padding: 4rem;
      box-shadow: rgba(50, 50, 93, 0.25) 0px 13px 27px -5px,
        rgba(0, 0, 0, 0.3) 0px 8px 16px -8px;
      gap: 1rem;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      form {
        display: flex;
        flex-direction: column;
        align-self: center;
        justify-content: center;
        gap: 1rem;
        input {
          border-radius: 2rem;
          border: none;
          padding: 0.5rem;
          color: #fff;
          background-color: var(--black-lighter);
        }
        ::placeholder {
          color: #fff;
        }
        span {
          text-decoration: underline;
          color: var(--black-lighter);
        }
        .submit {
          border-radius: 2rem;
          border: none;
          width: 4rem;
          background-color: var(--pink);
          color: #fff;
          padding: 0.5rem;
        }
      }
    }
  }
}
